@import url('https://fonts.googleapis.com/css2?family=Hina+Mincho&family=Zen+Kurenaido&display=swap');

.RecruitField{
    width: 100%;
    padding-top: 7%;
    font-family: 'Zen Kurenaido', sans-serif;
}

.RecruitField .Logo{
    font-size: 52px;
    font-weight: 600;
    margin-bottom: 60px;
    text-align: center;
}

.RecruitField .Explanation{
    text-align: center;
    background-color: #ffeddf;
    padding: 10px;
}

.RecruitField .RecruitBox{
    width: 50%;
    margin: 0 auto;
    margin-top: 80px;
    margin-bottom: 80px;
}

.RecruitField .RecruitBox .Box{
    display: flex;
    border-bottom: 2px dotted black;
    margin-left: 10px;
    margin-right: 10px;
    align-items: center;
}

.RecruitField .RecruitBox .Box:first-child{
    border-top: 2px dotted black;
}


.RecruitField .RecruitBox .Box .Left{
    width: 20%;
    text-align: center;
    font-size: 20px;
    padding: 25px;
}
