.FooterField{
    width: 100%;
    height: auto;
    background-color: rgb(248, 246, 246);
    text-align: center;
    padding-top: 50px;
}

.FooterField .Information{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    width: 80%;
    margin: 0 auto;
}

.FooterField .Information .Logo{
    margin-bottom: 20px;
}

.FooterField .Information .Logo img{
    width: 250px;
    height: 90px;
}

.FooterField .Information .Content .BoxContent{
    font-size: small;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    width: auto;
}

.FooterField .Information .Content .BoxContent .icon{
    margin-right: 5px;
}
