@import url('https://fonts.googleapis.com/css2?family=Hina+Mincho&family=Zen+Kurenaido&family=Noto+Serif+JP&display=swap');

.ContactField {
    width: 100%;
    padding-top: 7%;
    font-family: 'Zen Kurenaido', sans-serif;
}

.ContactField .Logo {
    font-size: 52px;
    font-weight: bold;
    font-family: 'Noto Serif JP', serif;
    text-align: center;
    margin-bottom: 80px;
}

.ContactField .FormField {
    width: 80%;
    margin: 0 auto;
    padding-top: 50px;
    padding-bottom: 50px;
}

.ContactField .FormField .FormContent {
    width: 90%;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}

.ContactField .FormField .FormContent .label {
    padding: 10px;
    width: 15%;
}

.ContactField .FormField .FormContent input {
    height: 25px;
    margin-top: 10px;
    margin-bottom: 10px;
    width: 60%;
}

.ContactField .FormField .FormContent textarea {
    height: 250px;
    margin-top: 10px;
    margin-bottom: 10px;
    width: 60%;
    resize: none;
}

.ContactField .FormField .FormContent .error {
    color: red;
    font-size: 14px;
    width: 100%;
    margin-left: 17%;
}

.ContactField .FormField .submit {
    width: 60%;
    margin: 0 auto;
    text-align: center;
    background-color: rgb(145, 219, 145);
    padding: 5px;
    margin-top: 10px;
    border: 1px solid #b6c9ab;
    color: white;
    border-radius: 10px;
}

@media screen and (max-width: 959px) {
    .ContactField {
        width: 100%;
        padding-top: 7%;
        font-family: 'Zen Kurenaido', sans-serif;
    }

    .ContactField .Logo {
        font-size: 32px;
        font-weight: bold;
        text-align: center;
        margin-bottom: 20px;
        margin-top: 12%;
    }

    .ContactField .FormField {
        width: 100%;
        margin: 0 auto;
        padding-top: 50px;
        padding-bottom: 50px;
    }

    .ContactField .FormField .FormContent {
        width: 100%;
        margin: 0 auto;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
    }

    .ContactField .FormField .FormContent .label {
        font-size: 14px;
        padding: 10px;
        width: 40%;
    }

    .ContactField .FormField .FormContent input {
        height: 25px;
        margin-top: 10px;
        margin-bottom: 10px;
        width: 100%;
    }

    .ContactField .FormField .FormContent textarea {
        height: 250px;
        margin-top: 10px;
        margin-bottom: 10px;
        width: 100%;
        resize: none;
    }

    .ContactField .FormField .FormContent .error {
        color: red;
        font-size: 14px;
        width: 100%;
        margin-left: 0%;
    }

    .ContactField .FormField .submit {
        width: 60%;
        margin: 0 auto;
        text-align: center;
        background-color: rgb(145, 219, 145);
        padding: 5px;
        margin-top: 10px;
        border: 1px solid #b6c9ab;
        color: white;
        border-radius: 10px;
    }
}