@import url('https://fonts.googleapis.com/css2?family=Hina+Mincho&family=Zen+Kurenaido&display=swap');
.HeaderField {
    position: fixed;
    height: auto;
    padding: 0;
    margin: 0;
    height: 13vh;
    z-index: 10;
    font-family: 'Zen Kurenaido', sans-serif;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.815);
}

.HeaderField .Logo {
    margin-top: 10px;
    margin-left: 60px;
    display: flex;
    color: #866b57;
    font-weight: 600;
    justify-content: left;
    font-size: 30px;
    font-family: 'Hina Mincho', serif;
    height: 80px;
    margin-bottom: 60px;
    
}
.HeaderField .Logo img{
    height: 90px;
}
.HeaderField .Burger{
    display: none;
}

.HeaderField .MenuField {
    position: absolute;
    display: flex;
    width: 58%;
    margin-top: -6%;
    margin-left: 40%;
    justify-content: space-around;
}

.HeaderField .MenuField .Menu{
    padding: 20px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    font-size: 20px;
    transition: all 0.3s ease;
    color: rgb(148, 90, 19);
}

.HeaderField .MenuField .Menu:hover{
    color: rgb(211, 172, 125);
    font-size: 24px;
}

@media screen and (max-width: 959px) {
	/* 480px以下に適用されるCSS（スマホ用） */
    .HeaderField{
        height: 80px;
        z-index: 10;
        font-family: 'Zen Kurenaido', sans-serif;
        width: 100%;
        background-color: rgba(255, 255, 255, 0.7);
        border-top-right-radius: 0;
    }

    a{
        text-decoration: none;
        color: #a59385;
      }

    .HeaderField .Logo {
        display: flex;
        color: #866b57;
        margin-top: 0;
        font-weight: bold;
        font-size: 20px;
        font-family: 'Hina Mincho', serif;
        justify-content: left;
        align-items: center;
        margin-left: 10%;
        height: 100%;
        width: 70%;
        border-bottom: none;
    }
    .HeaderField .Logo img{
        width: 100%;
        height: 75%;
    }
    .HeaderField .MenuField {
        font-size: 16px;
        width: 100%;
        color: #a59385;
        display: none;
        margin-left: -10px;
    }
    m{
        padding: 20px;
        cursor: pointer;
        position: relative;
        display: flex;
        align-items: center;
        width: 80%;
        transition: all 0.2s ease;
        margin-left: 0;
    }
    m::after {
        position: absolute;
        bottom: 5px;
        left: 0;
        content: '';
        width: 100%;
        height: 1px;
        background: #a59385;
        transform: scale(0, 0.8);
        transform-origin: right top;
        transition: transform .4s;
    
    }
    
    m:hover::after {
        transform: scale(0.6, 1);
        transform-origin: left top;
    }
    
    m:hover{
        left: 5px;
        font-size: 18px;
    }
    
    .HeaderField .ButtomField{
        display: none;
        position: absolute;
        top: 93%;
        width: 100%;
        margin: -50px;
    }
    
    .HeaderField .ButtomField .Link{
        display: flex;
        justify-content: center;
        margin-bottom: 25px;
    }
    
    .HeaderField .ButtomField .CopyRight{
        display: flex;
        justify-content: center;
        font-size: 14px;
    }

    .HeaderField .Burger{
        display: block;
    }
}