@import url('https://fonts.googleapis.com/css2?family=Hina+Mincho&family=Zen+Kurenaido&family=Noto+Serif+JP&display=swap');

.WorkField{
    width: 100%;
    overflow-x: hidden;
    overflow-y: hidden;
    padding-top: 7%;
    text-align: center;
    font-family: 'Zen Kurenaido', sans-serif;
}

.WorkField .Top{
    font-size: 52px;
    font-family: 'Noto Serif JP', serif;
    font-weight: bold;
    text-align: center;
    margin-bottom: 80px;
}

.WorkField .Explanation{
    margin-bottom: 50px;
    background-color: #ffeddf;
    padding: 25px;
}

.WorkField .Box{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 80%;
    margin: 0 auto;
    padding-bottom: 40px;
}

.WorkField .Box img{
    width: 600px;
    height: 350px;
}

.WorkField .Box h1{
    font-size: 28px;
    font-family: 'Noto Serif JP', serif;
    border-bottom: 1px solid black;
    margin-bottom: 50px;
}

.WorkField .Box .Left{
    margin-right: 30px;
}

.WorkField .Box .Right{
    background-color: #fafafa;
    width: 400px;
    height: 315px;
    padding: 20px;
}

@media screen and (max-width: 959px) {
    
.WorkField .Top{
    font-size: 32px;
    font-weight: bold;
    text-align: center;
    margin-bottom: 40px;
    margin-top: 15%;
}
    .WorkField .Box{
        display: block;
        justify-content: center;
        align-items: center;
        width: 100%;
        margin: 0 auto;
        padding-bottom: 40px;
    }
    
    .WorkField .Box img{
        width: 100%;
        height: 350px;
    }
    
    .WorkField .Box h1{
        font-size: 28px;
        border-bottom: 1px solid black;
        margin-bottom: 50px;
    }
    
    .WorkField .Box .Left{
        margin-right: 0px;
    }
    
    .WorkField .Box .Right{
        background-color: #fafafa;
        width: 100%;
        height: 315px;
        padding: 0px;
    }
}