@import url('https://fonts.googleapis.com/css2?family=Hina+Mincho&family=Zen+Kurenaido&display=swap');

body{
    margin: 0;
    background-color: rgb(255, 255, 255);
}

.TopField {
    /*background-image: url(../img/back.jpg);*/
    width: 100%;
    overflow-x: hidden;
    overflow-y: hidden;
    padding-top: 7%;
    position: relative;
}

.TopField .Slider {
    width: 100%;
    height: 85.8vh;
}

.TopField .Slider .Message {
    width: 90%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    color: white;
    font-family: 'Hina Mincho', serif;
    text-align: center;
    text-shadow: 3px 3px 0 rgb(0, 0, 0);
}

.TopField .Slider .Message h1 {
    font-size: 66px;
    text-shadow: 3px 3px 0 rgb(0, 0, 0);
    border-bottom: 3px solid white;
}

.TopField .Slider .Message p {
    font-size: 32px;
    font-weight: 600;
}

.TopField .Slider img {
    width: 100%;
    height: 100vh;
    opacity: 1;
}

.TopInformationField{
    position: relative;
    width: 100%;
    text-align: center;
    padding-top: 30px;
    padding-bottom: 80px;
    background-color: rgb(240, 241, 241);
    display: flex;
    justify-content: center;
}

.TopInformationField .Image{
    width: 50%;
}

.TopInformationField .Logo p{
    font-size: 40px;
    color: rgb(146, 146, 146);
    text-shadow: 3px 3px 0 rgb(212, 212, 212);
}

.TopInformationField .Message{
    display: flex;
    justify-content: center;
    width: 100%;
    height: 80px;
    padding-top: 20px;
}
.TopInformationField .Message .JumpBox{
    padding: 0px;
    width: 100%;
    height: 50px;
    border: 1px solid;
    border-radius: 10px;
    border-color:  rgb(250, 208, 153);
    background-color: rgb(250, 208, 153);
    transition: all 0.2s ease;
    cursor: pointer;
    color: rgb(255, 249, 249);
    font-size: 21px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.TopInformationField .Message .JumpBox:hover{
    background-color: rgb(252, 224, 187);
    color: rgb(112, 109, 109);
}

.MiddleMenu{
    width: 100%;
    text-align: center;
    padding-top: 40px;
    padding-bottom: 40px;
}

.MiddleMenu  table{
    margin-left: 0%;
    width: 100%;
    height: 300px;
}

.MiddleMenu  table td img{
    cursor: pointer;
    transition: all 0.2s ease;
}

.MiddleMenu  table td img:hover{
    width: 40%;
}
@media screen and (max-width: 959px) {

    body{
        overflow-x: hidden;
        max-width: 100%;
        box-sizing: border-box;
    }
    .TopField {
        background-image: none;
        position: relative;
        width: 100%;
        height: auto;
        top: 0;
    }

    .TopField .Slider {
        width: 100%;
        margin-left: 0;
    }

    .TopField .Slider .Message {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
        color: white;
        font-family: 'Hina Mincho', serif;
        text-align: center;
        text-shadow: 3px 3px 0 rgb(0, 0, 0);
    }

    .TopField .Slider .Message h1 {
        font-size: 40px;
        text-shadow: 3px 3px 0 rgb(0, 0, 0);
        border-bottom: 2px solid white;
    }

    .TopField .Slider .Message p {
        font-size: 22px;
        font-weight: 600;
    }

    .TopField .Slider img {
        width: 100%;
        height: 100vh;
        opacity: 1;
        clip-path: polygon(0 0, 100% 0%, 100% 100%, 0 100%, 0 10%);
    }
    .TopInformationField{
        width: 100%;
        text-align: center;
        padding-top: 30px;
        padding-bottom: 80px;
        background-color: rgb(240, 241, 241);
    
    }
    .TopInformationField .Image{
        width: 100%;
    }
    .TopInformationField .Image img{
        width: 100%;
    }
    
    .TopInformationField .Logo p{
        font-size: 40px;
        color: rgb(146, 146, 146);
        text-shadow: 3px 3px 0 rgb(212, 212, 212);
    }

    .TopInformationField .Message{
        display: flex;
        justify-content: center;
    }
    .TopInformationField .Message .JumpBox{
        margin-left: 0%;
        padding: 10px;
        width: 300px;
        border: 1px solid;
        border-radius: 10px;
        border-color:  rgb(250, 208, 153);
        background-color: rgb(250, 208, 153);
        transition: all 0.2s ease;
        cursor: pointer;
        color: rgb(255, 249, 249);
        font-size: 21px;
    }
    
    .TopInformationField .Message .JumpBox:hover{
        background-color: rgb(252, 224, 187);
        color: rgb(112, 109, 109);
    }
    
    .MiddleMenu{
        width: 90%;
        text-align: center;
        padding-top: 40px;
        padding-bottom: 40px;
    }
    
    .MiddleMenu  table{
        margin-left: 0%;
        width: 50%;
        height: 300px;
    }
    
    .MiddleMenu  table td img{
        cursor: pointer;
        transition: all 0.2s ease;
    }
    
    .MiddleMenu  table td img:hover{
        width: 100%;
    }
}

